import React, { useContext, useEffect, useRef, useState } from "react";
import { Calendar } from "primereact/calendar";
import { addLocale } from "primereact/api";
import { SearchContext } from "./SearchForm";

addLocale("th", {
  firstDayOfWeek: 0,
  showMonthAfterYear: false,
  dayNames: [
    "", // Sunday
    "", // Monday
    "", // Tuesday
    "", // Wednesday
    "", // Thursday
    "", // Friday
    "", // Saturday
  ],
  dayNamesShort: ["", "", "", "", "", "", ""],
  dayNamesMin: ["", "", "", "", "", "", ""],
  monthNames: [
    "", // January
    "", // February
    "", // March
    "", // April
    "", // May
    "", // June
    "", // July
    "", // August
    "", // September
    "", // October
    "", // November
    "", // December
  ],
  monthNamesShort: [
    "", // January
    "", // February
    "", // March
    "", // April
    "", // May
    "", // June
    "", // July
    "", // August
    "", // September
    "", // October
    "", // November
    "", // December
  ],
  today: "",
  clear: "",
});
const today = new Date();
today.setHours(0, 0, 0, 0);

export function CheckIn() {
  const { checkIn, setCheckIn } = useContext(SearchContext);
  const [value, setValue] = useState(null);
  const btnRef = useRef(null);
  useEffect(() => {
    if (checkIn) {
      const select = new Date(checkIn).toLocaleDateString("en-GB");
      setValue(select);
    } else {
      setValue(null);
    }
  }, [checkIn]);

  return (
    <>
      <style
        dangerouslySetInnerHTML={{
          __html: [
            ".ci .p-calendar .p-button-icon-only::before {",
            "  content: 'Check-In';",
            "}",
          ].join("\n"),
        }}
      ></style>
      <div style={{ width: "100%" }} className="ci">
        <Calendar
          ref={btnRef}
          value={checkIn}
          onChange={(e) => setCheckIn(e.value)}
          locale="th"
          showButtonBar
          dateFormat="dd/mm/yy"
          style={{ width: "100%" }}
          panelClassName="check-in-calendar"
          minDate={today}
          placeholder=""
          showIcon
          icon="pi pi-calendar-minus"
          prevIcon="pi pi-angle-left"
          nextIcon="pi pi-angle-right"
        />
      </div>
    </>
  );
}

export function CheckOut() {
  const { checkIn, checkOut, setCheckOut } = useContext(SearchContext);
  const [value, setValue] = useState(null);
  const btnRef = useRef(null);

  useEffect(() => {
    if (checkOut) {
      const select = new Date(checkOut).toLocaleDateString("en-GB");
      setValue(select);
    } else {
      setValue(null);
    }
  }, [checkOut]);

  useEffect(() => {
    if (checkIn) {
      const newDate = new Date(checkIn);
      newDate.setDate(new Date(checkIn).getDate() + 1);
      setCheckOut(newDate);
    }
  }, [checkIn]);
  return (
    <>
      <style
        dangerouslySetInnerHTML={{
          __html: [
            ".co .p-calendar .p-button-icon-only::before {",
            "  content: 'Check-Out';",
            "}",
          ].join("\n"),
        }}
      ></style>
      <div style={{ width: "100%" }} className="co">
        <Calendar
          ref={btnRef}
          value={checkOut}
          onChange={(e) => setCheckOut(e.value)}
          locale="th"
          showButtonBar
          dateFormat="dd/mm/yy"
          style={{ width: "100%" }}
          panelClassName="check-out-calendar"
          minDate={today}
          placeholder=""
          showIcon
          icon="pi pi-calendar-minus"
          prevIcon="pi pi-angle-left"
          nextIcon="pi pi-angle-right"
        />
      </div>
    </>
  );
}
